import { Injectable } from '@angular/core';
import { Observable, filter, map, take, tap } from 'rxjs';
import {
    ActivatedRouteSnapshot,
    CanActivate,
    CanActivateChild,
    Router,
    RouterStateSnapshot,
} from '@angular/router';
import { BoolFeature, ClientFeatures } from '@imunify360-api/features-management';
import { AuthState } from 'app/services/auth-state';
import { RequestsState } from 'app/services/requests-state';
import { UserFeaturesStatusService } from 'app/services/user-features-status';


@Injectable()
export class UserFeaturesStatusGuard implements CanActivate, CanActivateChild {
    constructor(
        private authState: AuthState,
        private requestsState: RequestsState,
        private userFeaturesStatusService: UserFeaturesStatusService,
        private router: Router,
    ) {}

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean>|boolean {
        if (this.authState.isAdmin.value) {
            return true;
        }
        return this.requestsState.hasEnabledFeatures.pipe(
            filter(hasEnabledFeatures => hasEnabledFeatures != null),
            take(1),
            tap((hasEnabledFeatures: boolean) => {
                if (!hasEnabledFeatures) {
                    this.router.navigate(this.authState.featuresDisabledCommands);
                }
            }),
        );
    }

    canActivateChild(
        route: ActivatedRouteSnapshot,
        state: RouterStateSnapshot,
    ): Observable<boolean>|boolean {
        if (this.authState.isAdmin.value) {
            return true;
        }
        return this.requestsState.clientFeatures.cache.pipe(
            take(1),
            map((features: ClientFeatures) => {
                const feature = route.data.feature;
                const enabled = features[feature] !== BoolFeature.na;
                if (!enabled) {
                    const children = route.parent!.routeConfig!.children || [];
                    const child = children.find((child) =>
                        !!child.data && features[child.data.feature] !== BoolFeature.na,
                    );

                    if (child) {
                        this.router.navigate(
                            this.authState.getDisabledRouteCommands(child),
                        );
                    }

                    return false;
                }
                return true;
            }),
        );
    }
}
