import { Component } from '@angular/core';
import { BackupSystemsService } from 'app/services/backup-systems';
import { AuthState } from 'app/services/auth-state';
import { RequestsState } from 'app/services/requests-state';
import { I360Config } from 'app/utils/config/config';
import { PermissionsService } from 'app/services/permissions';
import { take } from 'rxjs';

@Component({
    selector: 'i360-settings',
    templateUrl: './settings.component.html',
    styleUrls: ['../../settings/settings-index.component.scss'],
})
export class SettingsComponent {
    public restoreEnabled = false;
    readonly moreLink = 'https://blog.imunify360.com/how-to-manage-imunify-security-notifications';

    constructor(
        public backupSystemsService: BackupSystemsService,
        public authState: AuthState,
        public requestsState: RequestsState,
        public permissions: PermissionsService,
        public config: I360Config,
    ) {
        this.backupSystemsService.narrowStatus()
            .pipe(take(1))
            .subscribe(response => {
                this.restoreEnabled = response.data.items.enabled;
            });
    }
}
