import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ComingSoonComponent } from 'app/components/coming-soon/coming-soon.component';
import { SettingsComponent } from 'app/components/userside/settings/settings.component';
import { MaliciousComponent } from 'app/components/malware/malicious/malicious.component';
import { AppComponent } from 'app/components/application/app.component';
import { MalwareHistoryComponent } from 'app/components/malware/history/history.component';
import { IgnoreListComponent } from 'app/components/malware/ignore-list/ignore-list.component';
import { PROACTIVE_ROUTE } from 'app/components/proactive-defense/route';
import { FeatureName } from '@imunify360-api/features-management';
import { UserFeaturesStatusGuard } from './user-features-status.guard';
import { UnsavedConfigRouteGuard } from 'app/core/unsaved-route-guard';
import { FiltersService } from 'app/services/filters';
import { MalwareIndexComponent } from '../components/malware/malware-index.component';
import { MalwareOnDemandScanComponent } from 'app/components/malware/on-demand/on-demand-scan.component';

const routes: Routes = [
    {
        path: '',
        component: AppComponent,
        canActivate: [UserFeaturesStatusGuard],
        canActivateChild: [UserFeaturesStatusGuard],
        children: [
            {
                path: '',
                redirectTo: 'malware',
                pathMatch: 'full',
            },
            {
                path: 'malware',
                component: MalwareIndexComponent,
                data: {feature: FeatureName.av},
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                children: [
                    {path: '', redirectTo: 'files', pathMatch: 'full'},
                    {
                        path: 'files',
                        component: MaliciousComponent,
                        data: {feature: FeatureName.av},
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'history',
                        component: MalwareHistoryComponent,
                        data: {feature: FeatureName.av},
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'on-demand-scan',
                        component: MalwareOnDemandScanComponent,
                        data: {feature: FeatureName.av},
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'ignore-list',
                        component: IgnoreListComponent,
                        data: {feature: FeatureName.av},
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                ],
            },
            {
                path: PROACTIVE_ROUTE.path,
                loadChildren: PROACTIVE_ROUTE.loadChildren,
                data: {feature: FeatureName.proactive},
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'paramsOrQueryParamsChange',
            },
            {path: 'coming-soon', component: ComingSoonComponent},
            {
                path: 'settings',
                component: SettingsComponent,
                canDeactivate: [UnsavedConfigRouteGuard],
            },
        ],
    },
    {
        path: 'features-disabled',
        loadChildren: () => import('app/components/features-disabled/features-disabled.module')
            .then(m => m.FeaturesDisabledModule),
    },
];


@NgModule({
    imports: [
        RouterModule.forChild(routes),
    ],
    exports: [RouterModule],
})
export class ClientRoutingModule {
}
